@use "modules/core";

$_available-sizes: (
  core.$soul-s,
  core.$soul-m,
  core.$soul-l,
  core.$soul-xl,
  core.$soul-2-xl,
  core.$soul-3-xl
);
$_indicator-position: ("bottom": 20%, "right": 14%);
$_reference_position: ("top": -30%, "right": 11%);
$_indicator_size: 70%;
$_reference_size: 62%;


@mixin icon-size() {
  width: var(--soul-file-icon-size);
  height: var(--soul-file-icon-size);

  .soul-file-icon__icon {
    width: var(--soul-file-icon-size);
    height: var(--soul-file-icon-size);
  }
}

:host {
  --soul-file-icon-size: #{core.get-font-size(s)};
  display: flex;
  @include core.flexbox-center-horizontally-and-vertically();
  position: relative;
  @include icon-size();
}

:host([inline]),
:host([inline="true"]) {
  display: inline-flex;
}

:host([inline="false"]) {
  display: flex;
}

@each $size in $_available-sizes {
  :host([size="#{$size}"]) {
    --soul-file-icon-size: #{core.get-font-size($size)};
    @include icon-size();
  }
}

.soul-file-icon__indicator {
  position: absolute;
  bottom: map-get($_indicator-position, "bottom");
  right: map-get($_indicator-position, "right");
  transform: translate(50%, 50%);
  width: $_indicator_size;
  height: $_indicator_size;
}

