import {Component, ComponentWillLoad, Element, h, Host, Prop, Watch} from '@stencil/core';
import {IconMap} from './IconMap';
import {getAssetsFolder} from '../../services/AssetsFolderHelper';
import {StatusHelper} from '../../services/StatusHelper';
import {isDefined} from '../../utils/utils';
import {SizeHelper} from '../../services/SizeHelper';

@Component({
  tag: 'soul-file-icon',
  styleUrl: 'soul-file-icon.scss',
  shadow: true
})
export class SoulFileIcon implements ComponentWillLoad {
  @Prop({reflect: true}) format: string = IconMap.DEFAULT_ICON;
  @Prop({reflect: true}) status: 'positive' | 'warning' | 'critical' | 'not-allowed' | 'waiting';
  @Prop({reflect: true}) size: 's' | 'm' | 'l' | 'xl' | '2-xl' | '3-xl' = 's';
  @Prop({reflect: true}) inline: boolean = false;
  @Prop({reflect: true}) reference: boolean;
  @Element() el: HTMLElement;
  private iconMap: IconMap;
  private supportedSizes = ['s', 'm', 'l', 'xl', '2-xl', '3-xl'];
  private supportedStatuses = ['positive', 'warning', 'critical', 'not-allowed', 'waiting'];
  private assetsFolder: string;
  private statusHelper = new StatusHelper();
  private sizeHelper = new SizeHelper();

  @Watch('size')
  public sizeChange(newValue: string): void {
    this.sizeHelper.validateSize(this.supportedSizes, newValue);
  }

  @Watch('status')
  public statusChange(newValue: string): void {
    this.statusHelper.validateStatus(this.supportedStatuses, newValue);
  }

  public componentWillLoad(): void {
    this.assetsFolder = getAssetsFolder(this.el);
    this.iconMap = new IconMap(this.assetsFolder);
    this.sizeHelper.validateSize(this.supportedSizes, this.size);
    this.statusChange(this.status);
  }

  public render() {
    const iconPath = this.iconMap.getIconPath(this.format?.trim());
    return <Host>
      <svg class="soul-file-icon__icon">
        <use
          href={iconPath}></use>
      </svg>
      {isDefined(this.status) && this.statusHelper.isValidStatus(this.supportedStatuses, this.status) ? <div class="soul-file-icon__indicator">
        <soul-indicator type={this.status} border></soul-indicator>
      </div> : ''
      }
      {this.reference ? <div class="soul-file-icon__reference"></div> : ''}
    </Host>;
  }

}
